.wrapper {
    background: #fff;
}
.info-block {
    @include rem((padding: 60px 0 30px 0));
    hr {
        @include rem((margin-top: 60px));
    }
}

.responsible-block {
    @include rem((padding: 30px 0 30px 0));
    h3 {
        @include rem((margin-bottom: 20px));
        font-weight: bold;
    }
    ul {
        li {
            list-style-type: none;
            font-weight: bold;
            a {
                color: $color-black-medium;
                span {
                    font-weight: normal;
                }
            }
            span {
                font-weight: normal;
            }
        }
    }
    hr {
        @include rem((margin-top: 60px));
    }
}

